import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { SolicitudDetalleRespuestas } from '../../interfaces/aerc-interfaces/solicitud-detalle.interface';
import { SolicitudModel } from '../../models/aerc-models/solicitud-model';
import { SolicitudInnovacionDetalleModel } from '../../models/aerc-models/solicitud-innovacion-detalle.model';

@Injectable({
  providedIn: 'root'
})
export class SolicitudInnovacionDetalleService {

  private urlBase = `${environment.backend.aerc}api/solicitud-innovacion`;
  constructor(
    private http: HttpClient,
  ) { }

  public findDetalleByIdSolicitud(solicitud: SolicitudModel): Observable<SolicitudInnovacionDetalleModel> {
    return this.http.post<SolicitudInnovacionDetalleModel>(`${this.urlBase}/find`, solicitud);
  }

}
