import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { SolicitudDetalleRespuestas } from '../../interfaces/aerc-interfaces/solicitud-detalle.interface';
import { DocumentosModel } from '../../models/aerc-models/documentos-model';
import { SolicitudModel } from '../../models/aerc-models/solicitud-model';

@Injectable({
  providedIn: 'root'
})
export class SolicitudDetalleService {

  constructor(
    private http: HttpClient,
  ) { }

  public getRespuestasBySolicitud(solicitud: SolicitudModel): Observable<SolicitudDetalleRespuestas> {
    return this.http.post<SolicitudDetalleRespuestas>(`${environment.backend.aerc}api/detalle/v1/solicitud-respuestas`, solicitud);
  }

  public eliminarDocumentacion(documento: DocumentosModel): Observable<boolean> {
    return this.http.post<boolean>(`${environment.backend.aerc}api/detalle/delete/documentacion`, documento);
  }

}
