import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducer';
import Swal from 'sweetalert2';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { FormControl, FormGroup } from '@angular/forms';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { SolicitudModel } from 'src/app/commons/models/aerc-models/solicitud-model';
import { EstadoSolicitudModel } from 'src/app/commons/models/aerc-models/estado-solicitud-model';
import { SolicitudService } from 'src/app/commons/services/aerc-services/solicitud.service';
import { listarSolicitudes, listarSolicitudesInnovacion, setVistaSolicitud } from 'src/app/store/actions/aerc-actions/solicitud.actions';
import { getEstadoSolicitud } from 'src/app/store/actions/aerc-actions/estado-solicitud.actions';
import { TipoSolicitudModel } from 'src/app/commons/models/aerc-models/tipo-solicitud-model';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { ComentariosDialogComponent } from 'src/app/commons/components/comentarios-dialog/comentarios-dialog.component';
import { PeriodoAcademicoModel } from 'src/app/commons/models/aerc-models/periodo-academico-model';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { PeriodoAcademicoService } from 'src/app/commons/services/aerc-services/periodo-academico.service';
import { AuthenticationService } from 'src/app/commons/services/authentication.service';
import { environment } from 'src/environments/environment';
import { SolicitudInnovacionDetalleService } from '../../../../commons/services/aerc-services/solicitud-innovacion-detalle.service';
import { SolicitudInnovacionDetalleModel } from '../../../../commons/models/aerc-models/solicitud-innovacion-detalle.model';
import { DocumentosModel } from 'src/app/commons/models/aerc-models/documentos-model';
import { SolicitudDetalleService } from '../../../../commons/services/aerc-services/solicitud-detalle.service';
import { SolicitudDetalleRespuestas } from 'src/app/commons/interfaces/aerc-interfaces/solicitud-detalle.interface';
import { AprobadorAercModel } from '../../../../commons/models/aerc-models/aprobador-aerc.model';
import { AprobadorAercService } from '../../../../commons/services/aerc-services/aprobador-aerc.service';
import { ResolucionAercModel } from '../../../../commons/models/aerc-models/resolucion-aerc.model';
import { ResolucionesService } from '../../../../commons/services/aerc-services/resoluciones.service';

@Component({
  selector: 'app-index-solicitud-academica',
  templateUrl: './index-solicitud-academica.component.html',
  styles: [
  ]
})
export class IndexSolicitudAcademicaComponent implements OnInit {

  public _subscriptionSolicitudState: Subscription;
  public _subscriptionEstadoSolicitudState: Subscription;
  public listadoSolicitudes: SolicitudModel[] = [];

  //*
  public formTopBar: FormGroup;
  public searchPagination: SearchPagination<string> = null;
  public pageSize: number = 10;
  public buscador = "";
  public page: number = 1;
  public totalElements = 0;
  public tipo: number = 0;
  public estado: number = 0;
  public periodo: number = 0;
  public listaEstadoSolicitud: Array<EstadoSolicitudModel> = [];
  public listaTipoSolicitud: Array<TipoSolicitudModel> = [];
  public listaPeriodoAcademico: Array<PeriodoAcademicoModel> = [];
  //*

  //*
  public isUsuarioAprobador: boolean = false;
  public solicitudInnovacion: SolicitudInnovacionDetalleModel = null;
  public revisarEstado: boolean = false;
  public solicitudDocumentacion: Array<DocumentosModel> = [];
  public puedeAprobar: boolean = false;
  public aprobador: Array<AprobadorAercModel> = [];
  public resolucion: ResolucionAercModel = null;
  public tieneResolucion: boolean = false;
  public isCreacionEstudios: boolean = false;
  //*

  private timer = null;


  public loading = false;
  @BlockUI('windowsIndexSolicitudAcademica') blockUI: NgBlockUI;


  constructor(
    private store: Store<AppState>,
    private solicitudService: SolicitudService,
    private router: Router,
    private alert: UtilAlert,
    private dialog: MatDialog,
    private periodoAcademicoService: PeriodoAcademicoService,
    private auth: AuthenticationService,
    private solicitudInnovacionService: SolicitudInnovacionDetalleService,
    private detalleService: SolicitudDetalleService,
    private aprobadorService: AprobadorAercService,
    private resolucionService: ResolucionesService

  ) {
    if (this.auth.esRol(`ROLE_AERC_APROBADOR_INNOVACION_${environment.id_sistema}`)) {
      this.isUsuarioAprobador = true;
    }
  }

  //* INICIACION DE LA VENTANA
  //* ----------------------------------------------------------------------------------

  ngOnInit(): void {
    this.crearTopBar();
    this.listarSolicitudes();
    this.listarEstadoSolicitudes();
    this.listarTipoSolicitud();
    this._solicitudState();
    this._estadoSolicitudState();
    this.getAprobador();
  }

  ngOnDestroy(): void {
    this._subscriptionSolicitudState?.unsubscribe();
    this._subscriptionEstadoSolicitudState?.unsubscribe();
  }

  crearTopBar() {
    this.formTopBar = new FormGroup({
      estado: new FormControl(""),
      tipo: new FormControl(""),
      registros: new FormControl("")
    });

    this.formTopBar.get("estado").setValue(this.estado.toString());
    this.formTopBar.get("tipo").setValue(this.tipo.toString());
    this.formTopBar.get("registros").setValue(this.pageSize.toString());
  }

  //* STATE REDUX
  //* ----------------------------------------------------------------------------------

  private _solicitudState() {
    this._subscriptionSolicitudState = this.store.select("solicitud").subscribe((state) => {
      // console.log("INICIO SOLICITUD STATE");
      this.loading = state.loading;

      if (state.searchPagination != null) {
        this.searchPagination = state.searchPagination;
        this.page = state.searchPagination.page;
        this.pageSize = state.searchPagination.records;
        this.buscador = state.searchPagination.seek;
        this.estado = state.estadoSolicitud;
        this.periodo = state.periodo;
        this.tipo = state.tipoSolicitud;
        if (this.formTopBar.get("tipo").value != this.tipo.toString()) {
          this.formTopBar.get("tipo").setValue(this.tipo);
          this.page = 1;
        }

        if (this.formTopBar.get("estado").value != this.estado.toString()) {
          this.formTopBar.get("estado").setValue(this.estado);
          this.page = 1;
        }

        if (this.formTopBar.get("registros").value != this.pageSize.toString()) {
          this.formTopBar.get("registros").setValue(this.pageSize);
        }
      }

      if (state.listadoSolicitudesPaginacion) {
        this.listadoSolicitudes = state.listadoSolicitudesPaginacion.content;
        this.totalElements = state.listadoSolicitudesPaginacion.totalElements;
      }

      if (this.blockUI.isActive != state.loading) {
        // console.log("Buscando solicitudes....");
        state.loading ? this.blockUI.start("Buscando solicitudes...") : this.blockUI.stop();
      }
    })
  }

  private _estadoSolicitudState() {
    this._subscriptionEstadoSolicitudState = this.store.select('estadoSolicitud').subscribe((state) => {
      if (state.estados) {
        this.listaEstadoSolicitud = state.estados;
      }
    })
  }

  //* METODOS PARA LISTAR Y CAMBIAR LAS SOLICITUDES
  //* ----------------------------------------------------------------------------------

  listarSolicitudes() {
    this.listadoSolicitudes = [];
    if (this.searchPagination == null) {
      this.searchPagination = {
        page: this.page,
        records: this.pageSize,
        seek: this.buscador,
      }
    } else {
      this.searchPagination = null
      this.listarSolicitudes();
    }
    this.store.dispatch(
      listarSolicitudesInnovacion({ searchPagination: this.searchPagination, estadoSolicitud: this.estado, tipoSolicitud: this.tipo, isAprobador: this.isUsuarioAprobador }))
  }

  listarEstadoSolicitudes() {
    this.store.dispatch(getEstadoSolicitud({ tipoSolicitud: 'INNOVACION' }));
  }

  listarTipoSolicitud() {
    this.solicitudService.getAllTipoSolicitudAcademica().subscribe(result => {
      if (result != null) {
        this.listaTipoSolicitud = result;
      }
    })
  }

  listarPeriodoAcademico() {
    this.periodoAcademicoService.findAll().subscribe(result => {
      if (result != null) {
        this.listaPeriodoAcademico = result;
      }
    })
  }

  cambiarTipo(tipoServicio: number) {
    this.tipo = tipoServicio;
    this.listarSolicitudes();
  }

  cambiarEstado(estado: number) {
    this.estado = estado;
    this.listarSolicitudes();
  }

  cambiarPeriodo(periodo: number) {
    this.periodo = periodo;
    this.listarSolicitudes();
  }

  buscar(texto: string) {
    this.buscador = texto;
    if (this.searchPagination != null) {
      this.searchPagination = { ...this.searchPagination, seek: this.buscador }
    }
    /**
  .    * Se usa para evitar una petición cada vez que se presione una tecla y se haga solo cuando se termine de escribir.
     **/
    if (this.timer != null) clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      this.listarSolicitudes();
      this.timer == null;
    }, 1000);
  }

  getAprobador() {
    this.aprobadorService.findAprobadorInnovacion().subscribe(result => {
      if (result != null) {
        this.aprobador = result;
      }
    })
  }

  getStatusAprobador() {
    this.aprobador.forEach(a => {
      switch (a.tipoAprobador.nombre) {
        case "DIRECCIÓN DE CURRÍCULO":
          if (this.solicitudInnovacion.statusDirCur == "SIN REVISAR") {
            this.puedeAprobar = true;
            // console.log("PUEDE APROBAR: CUR");
          }
          break;

        case "DIRECCIÓN ACADÉMICA":
          if (this.solicitudInnovacion.statusDirCur == "APROBADA" && this.solicitudInnovacion.statusDirAca == "SIN REVISAR") {
            this.puedeAprobar = true;
            // console.log("PUEDE APROBAR: ACA");
          }
          break;

        case "VICERRECTORÍA ACADÉMICA":
          if (this.solicitudInnovacion.statusDirCur == "APROBADA" && this.solicitudInnovacion.statusDirAca == "APROBADA" && this.solicitudInnovacion.statusVra == "SIN REVISAR") {
            this.puedeAprobar = true;
            // console.log("PUEDE APROBAR: VRA");
          }
          break;
        default:
          break;
      }
    })

  }

  verSolicitud(solicitud: SolicitudModel) {
    // console.log(solicitud)
    this.store.dispatch(setVistaSolicitud({ solicitud: solicitud }));
    this.router.navigate([`aerc/listado-solicitudes/ver-solicitud`]);
  }

  verEstadoSolicitud(solicitud: SolicitudModel) {
    this.solicitudInnovacionService.findDetalleByIdSolicitud(solicitud).subscribe(result => {
      this.solicitudInnovacion = result;
      this.cargarSolicitudDetalle();
      this.revisarEstado = true;
      this.getStatusAprobador();
      this.cargarResolucion();

      this.isCreacionEstudios = result.idSolicitud.tipoSolicitud.id == 6 ? true : false;
    })
  }

  private cargarSolicitudDetalle() {
    this.blockUI.start("Cargando Detalle...");
    this.detalleService.getRespuestasBySolicitud(this.solicitudInnovacion.idSolicitud).subscribe(result => {
      if (result != null) {
        this.blockUI.stop();
        this.autocompletarDocumentacion(result);
      }
    })
  }

  private cargarResolucion() {
    // console.log(this.solicitudInnovacion);
    this.resolucionService.getResolucionByIdSolicitud(this.solicitudInnovacion.idSolicitud.id).subscribe(result => {
      if (result != null) {
        this.tieneResolucion = true;
        this.resolucion = result;
        // console.log(result);
      }
    })
  }

  private autocompletarDocumentacion(soliicitudDetalle: SolicitudDetalleRespuestas): void {
    soliicitudDetalle.solicitudDocumentacion.forEach(documento => {
      let doc = { ...documento };
      doc.file = this.Base64toFile(doc.base64, doc.nombre, doc.tipo);
      this.solicitudDocumentacion.push(doc);
    })
  }

  private fileToBase64 = (file: File): Promise<string> => {
    return new Promise<string>((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result.toString());
      reader.onerror = error => reject(error);
    })
  }

  private Base64toFile(base64: string, nombre: string, tipo: string) {
    let arr = base64.split(','),
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], nombre, { type: tipo });
  }

  downloadFile(data: File) {
    const blob = new Blob([data], { type: data.type });
    // console.log(blob);
    const url = window.URL.createObjectURL(blob);
    window.open(url);
  }

  downloadFileResolucion(resolucion: ResolucionAercModel) {
    let src = `data:application/pdf;base64,${resolucion.pdfResolucion}`;
    let downloadLink = document.createElement("a");
    let name = "Resolucion N°" + resolucion.numResolucion + " - " + resolucion.solicitud.aprobadorGenerador.usuario + ".pdf";
    downloadLink.href = src;
    downloadLink.download = name;
    downloadLink.click();
  }

  verComentarios() { }

  public volver(): void {
    this.solicitudInnovacion = null;
    this.solicitudDocumentacion = [];
    this.revisarEstado = false;
    this.tieneResolucion = false;
  }

  aprobarSolicitud(solicitud: SolicitudModel) {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "La solicitud sera aprobada.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        this.blockUI.start("Aprobando solicitud...");
        this.solicitudService.aprobarSolicitud(solicitud).subscribe(result => {
          if (result != null) {
            this.blockUI.stop();
            this.alert.successSwalHtml("Solicitud aprobada con éxito");
            this.listarSolicitudes();
            this.volver();
          }
        }, (_error) => {
          this.blockUI.stop();
          this.alert.toastError(_error?.error?.message);
        })
      }
    })
  }

  rechazarSolicitud(solicitud: SolicitudModel) {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "La solicitud sera rechazada.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        this.blockUI.start("Rechazando solicitud...");
        this.solicitudService.rechazarSolicitudCurricular(solicitud).subscribe(result => {
          if (result != null) {
            this.blockUI.stop();
            this.alert.successSwalHtml("Solicitud rechazada con éxito");
            this.listarSolicitudes();
            this.volver();
          }
        }, (_error) => {
          this.blockUI.stop();
          this.alert.toastError(_error?.error?.message);
        })
      }
    })
  }

  openDialogComentarios(solicitudInnovacion: SolicitudInnovacionDetalleModel, isAprobar: boolean) {
    // console.log(this.solicitudInnovacion);
    let solicitud = this.solicitudInnovacion.idSolicitud;
    // console.log(solicitud);
    const dialogRef = this.dialog.open(ComentariosDialogComponent, {
      data: { alumno: solicitud.facultadId, tipo: solicitud.tipoSolicitud.descripcion, id: solicitud.id }
    });

    dialogRef.afterClosed().subscribe(result => {
      let comentarios: string = result;
      let solicitudTemp = { ...solicitud };
      solicitudTemp.comentarioInnovacion = comentarios;
      isAprobar ? this.aprobarSolicitud(solicitudTemp) : this.rechazarSolicitud(solicitudTemp);
    });
  }

  openDialogComentariosTest(solicitudInnovacion: SolicitudInnovacionDetalleModel, accion: string) {
    let solicitud = this.solicitudInnovacion.idSolicitud;
    const dialogRef = this.dialog.open(ComentariosDialogComponent, {
      data: { alumno: solicitud.facultadId, tipo: solicitud.tipoSolicitud.descripcion, id: solicitud.id }
    });

    dialogRef.afterClosed().subscribe(result => {
      let comentarios: string = result;
      let solicitudTemp = { ...solicitud };
      solicitudTemp.comentarioInnovacion = comentarios;
      switch (accion) {
        case "APROBAR":
          this.aprobarSolicitud(solicitudTemp);
          break;
      
        case "RECHAZAR":
          this.rechazarSolicitud(solicitudTemp);
          break;

        case "EDITAR":
          this.habilitarEdicionSolicitud(solicitudTemp);
          break;
        default:
          break;
      }
    });
  }

  habilitarEdicionSolicitud(solicitud: SolicitudModel) {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "Se habilitará la edición de la solicitud.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        this.solicitudService.habilitarEdicion(solicitud).subscribe(result => {
          if (result != null) {
            this.alert.successSwalHtml("Solicitud habilitada para su edición");
            this.listarSolicitudes();
            this.volver();
          }
        }, (_error) => {
          this.alert.toastError(_error?.error?.message);
        })
      }
    })
  }

  editarSolicitud(solicitud: SolicitudModel) {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "Se editará la solicitud. Esta acción no se puede deshacer, los cambios realizados seran permanentes.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        if (this.isUsuarioAprobador) {
          this.solicitudService.habilitarEdicion(solicitud).subscribe(result => {
            if (result != null) {
              this.alert.toastInfo("Solicitud habilitada para edición");
              this.router.navigate([`/aerc/solicitudes-academicas/solicitud/${solicitud.id}/aprobEdit`]);
            }
          }, (_error) => {
            this.alert.toastError(_error?.error?.message);
          })
        } else {
          this.alert.toastInfo("EDITANDO SOLICITUD...");
          this.router.navigate([`/aerc/solicitudes-academicas/solicitud/${solicitud.id}/aprobEdit`]);
        }

      }
    })
  }


}
